import loadPage from '../util/loadPage'
import '../style/pages/page_8.less'

export default function(){
    var page=loadPage(7);
    return new Promise(()=>{
        console.log(window.score);
        page.find('.red .slider .slider-inner').css('width',`${window.score/4}%`);
        page.find('.red .slider .num').text(`${Math.round(window.score/4)}%`);

        page.find('.blue .slider .slider-inner').css('width',`${Math.floor(window.lastDayP*100)}%`);
        page.find('.blue .slider .num').text(`${Math.floor(window.lastDayP*100)}%`);

        page.find('.result .time').text(window.lastDay);
        if(window.score/4<25){
            page.find('.result .des').html('<p></p><p></p><p></p>');
        }else if(window.score/4<50){
            page.find('.result .des').html('<p>~</p><p></p>');
        }else if(window.score/4<75){
            page.find('.result .des').html('<p></p><p></p><p></p>');
        }else{
            page.find('.result .des').html('<p></p><p></p><p></p><p></p>');
        }
        page.find('.clicker').click(()=>{
            $('.share').show();
        });
        $('.share').click(()=>{
            $('.share').hide();
        });
    });
}


